import { Fragment } from "react";
import type { Metadata } from "next";
import {
  BannerSection,
  CategoriesSection,
  DailyBestDownloaded,
  DailyBestSeller,
  DailyFeatureSection,
  DailyCustomerReviewSection,
  TopSellingLists,
  CategoriesFeatureSection,
} from "@/app/views/main/(views)/(home)/components";

// Metadata for home page
export const metadata: Metadata = {
  title: "Trang chủ",
  description:
    "Khám phá và tải xuống nội dung số chất lượng cao. BanVe - Nền tảng mua bán tài nguyên thiết kế hàng đầu Việt Nam.",
  keywords: [
    "digital content",
    "marketplace",
    "design resources",
    "templates",
    "graphics",
    "vector",
    "tải xuống",
    "thiết kế",
  ],
  openGraph: {
    title: "BanVe - Nền tảng mua bán nội dung số",
    description: "Khám phá và tải xuống nội dung số chất lượng cao",
    type: "website",
    url: "/",
  },
};

// Revalidate every hour
export const revalidate = 3600;

export default async function HomePage() {
  return (
    <Fragment>
      <div className="bg-gray-50 py-4">
        <div className="container mx-auto py-2">
          <CategoriesFeatureSection />
        </div>
      </div>
      <BannerSection />
      <div className="container mx-auto py-8">
        <DailyFeatureSection />
      </div>
      <div className="container mx-auto py-8">
        <TopSellingLists />
      </div>
      <div className="container mx-auto py-8">
        <DailyBestSeller />
      </div>
      <div className="container mx-auto py-8">
        <DailyBestDownloaded />
      </div>
      <div className="container mx-auto py-8">
        <CategoriesSection />
      </div>
      <div className="container mx-auto py-8">
        <DailyCustomerReviewSection />
      </div>
    </Fragment>
  );
}
